.signInOrSignUp nav{
  padding: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 18px;
}
.signInOrSignUp nav label{
  background: #fff;
  padding: 6px;
  margin: 0 5px;
  position: relative;
}
.signInOrSignUp nav label .checked{
  color: #2C8EBB;
  border-bottom: 2px solid #2C8EBB;
}


.signInOrSignUp nav label:hover{
  color: #777;
}
.signInOrSignUp nav input[type="radio"]{
  display: none;
}




