.TodoItem{
  width: 48em;
   padding: .75em;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-align: center;
       align-items: center;
}
.TodoItem input[type="checkbox"]{
  display: none;
}
.TodoItem i{
  font-size: 2em;
  cursor: pointer;
}

 .TodoItem>.title{
   -ms-flex-positive: 1;
       flex-grow: 1;
   padding: 0 .5em;
   max-width: 48em;
}
.TodoItem>.completed~.title{
  text-decoration: line-through;
  color: #888;
}
.TodoItem>button{
  font-size: 19px;
  cursor: pointer;
}
.TodoItem>button:hover{
  color: red;
}



